import React, { useState, useEffect, useRef, useContext } from "react";
import { useNavigate } from "react-router-dom";
import * as Api from "../../api";

import {
  Box,
  TextField,
  Stack,
  Button,
  Avatar,
  IconButton,
  Badge,
  Switch,
} from "@mui/material";
import { styled } from "@mui/material/styles";
import GitHubIcon from "@mui/icons-material/GitHub";
import InstagramIcon from "@mui/icons-material/Instagram";
import WysiwygIcon from "@mui/icons-material/Wysiwyg";
import PersonRemoveIcon from "@mui/icons-material/PersonRemove";

import { DispatchContext } from "../../App";
import { sendFile, defaultImage, getImageBaseUrl } from "../../utils";
import AlertDialog from "../utils/AlertDialog";

//  export   

function UserEditForm({ user, setIsEditing, setUser }) {
  const navigate = useNavigate();
  const dispatch = useContext(DispatchContext);
  const inputRef = useRef();

  const [form, setForm] = useState({
    name: "",
    email: "",
    description: "",
  }); //  name, email, description form  state 
  const [emailPermission, setEmailPermission] = useState(true); // email   state
  const [descPermission, setDescPermission] = useState(true); // description   state
  const [userImage, setUserImage] = useState(user.profile); //    .
  const [isDeleteAccount, setIsDeleteAccount] = useState(false); //      .

  const [snsURL, setSnsURL] = useState({
    github: '',
    instagram: '',
    blog: '',
  })

  const imageBaseUrl = getImageBaseUrl(); //  baseUrl .

  useEffect(() => {
    Api.get("users/current").then((res) => {
      const result = res.data.user;
      setForm((cur) => {
        const newForm = {
          ...cur,
          email: result.email,
          description: result.description,
          name: result.name,
        };
        return newForm;
      });
      setEmailPermission(result.permission.email);
      setDescPermission(result.permission.description);
      setSnsURL((cur) => {
        const newSNS = {
          ...cur,
          github: result.sns.github,
          instagram: result.sns.instagram,
          blog: result.sns.blog,
        };
        return newSNS;
      })
    });
  }, []); //  form  data , email/description  

  const handleSubmit = async (e) => {
    e.preventDefault();

    // "users/id"  PUT .
    const res = await Api.put("users", {
      name: form.name,
      email: form.email,
      description: form.description,
      sns: {github: snsURL.github, instagram: snsURL.instagram, blog: snsURL.blog},
      permission: { email: emailPermission, description: descPermission },
      profile: userImage,
    });
    //   response data.
    const updatedUser = res.data.user;

    //    user .
    setUser(updatedUser);

    // isEditing false .
    setIsEditing(false);
  };

  const deleteAccountBtnClick = async (isDeleting) => {
    if (isDeleting) {
      //   .
      await Api.delete("users");
      // sessionStorage   JWT  .
      sessionStorage.removeItem("userToken");
      // dispatch   .
      dispatch({ type: "LOGOUT" });
      //   .
      navigate("/login");
    }
  };

  //      .
  const setImage = async (e) => {
    const formData = new FormData();
    formData.append("image", e.target.files[0]);

    const res = await sendFile(formData);
    setUserImage(res.data.profiles.filename);
  };

  //  src    /     .
  const avatar = (
    <Avatar
      component="span"
      alt="Remy Sharp"
      src={userImage === defaultImage ? user.profile : imageBaseUrl + userImage}
      sx={{ ...shapeStyles, ...shapeCircleStyles }}
    />
  );

  return (
    <Box
      component="form"
      onSubmit={handleSubmit}
      sx={{ mt: 1, width: "400px" }}
    >
      <Stack spacing={2} align="center">
        <label htmlFor="icon-button-file">
          <Input
            accept="image/*"
            id="icon-button-file"
            type="file"
            ref={inputRef}
            onChange={setImage.bind(this)}
          />
          <IconButton
            color="primary"
            aria-label="upload picture"
            component="span"
          >
            <Badge
              color="primary"
              overlap="circular"
              badgeContent="+"
              sx={{ bgColor: "#C7A27C" }}
            >
              {avatar}
            </Badge>
          </IconButton>
        </label>
        <Stack sx={{ display: "inline" }}>
          <TextField
            required
            label=""
            onChange={(e) => setForm({ ...form, name: e.target.value })}
            value={form.name}
            sx={{ width: "375px" }}
          />
        </Stack>
        <Stack style={{ display: "inline" }}>
          <TextField
            disabled
            type="email"
            label=""
            value={form.email}
            sx={{ width: "320px" }}
          />
          <Switch
            {...label}
            checked={emailPermission}
            onChange={(e) => setEmailPermission(e.target.checked)}
          />
        </Stack>
        <Stack style={{ display: "inline" }}>
          <TextField
            required
            label=", "
            onChange={(e) => setForm({ ...form, description: e.target.value })}
            value={form.description}
            sx={{ width: "320px" }}
          />
          <Switch
            {...label}
            checked={descPermission}
            onChange={(e) => setDescPermission(e.target.checked)}
          />
        </Stack>
        <Stack style={{ display: "inline" }}>
          <TextField label="git URL" sx={{ width: "320px" }} value={snsURL.github} onChange={(e)=> setSnsURL({...snsURL, github: e.target.value})}/>
          <GitHubIcon sx={iconStyles} />
        </Stack>
        <Stack style={{ display: "inline" }}>
          <TextField label="instar URL" sx={{ width: "320px" }} value={snsURL.instagram} onChange={(e)=> setSnsURL({...snsURL, instagram: e.target.value})}/>
          <InstagramIcon sx={iconStyles} />
        </Stack>
        <Stack style={{ display: "inline" }}>
          <TextField label="blog URL" sx={{ width: "320px" }} value={snsURL.blog} onChange={(e)=> setSnsURL({...snsURL, blog: e.target.value})}/>
          <WysiwygIcon sx={iconStyles} />
        </Stack>
      </Stack>
      <Stack
        direction="row"
        spacing={2}
        sx={{ mt: 2, justifyContent: "center" }}
      >
        <Button variant="contained" type="submit">
          
        </Button>{" "}
        <Button
          type="reset"
          onClick={() => setIsEditing(false)}
          variant="outlined"
        >
          
        </Button>
      </Stack>
      <Button
        variant="text"
        startIcon={<PersonRemoveIcon />}
        sx={{ float: "right", color: "#7e7e7e" }}
        onClick={() => setIsDeleteAccount(true)}
      >
         
      </Button>
      {isDeleteAccount && (
        <AlertDialog checkDeleteComplete={deleteAccountBtnClick} />
      )}
    </Box>
  );
}

export default UserEditForm;

const Input = styled("input")({
  display: "none",
});
const iconStyles = {
  marginLeft: "25px",
  marginTop: "15px",
  marginRight: "10px",
};
const shapeStyles = { width: 150, height: 150 };
const shapeCircleStyles = { borderRadius: "50%" };

const label = { inputProps: { "aria-label": "Switch demo" } };
