import React, { useState, useContext } from "react";
import * as Api from "../../api";
import { UserStateContext } from "../../App";

import { Box, TextField, Stack, Button } from "@mui/material";

//    {   state}, {  } props .
function AwardAddForm({ setAddAward, setAwardLists }) {
  //   ,   userid .
  const userState = useContext(UserStateContext);
  const userId = userState.user.id;

  // ,  state .
  const [awardTitle, setAwardTitle] = useState("");
  const [awardDetail, setAwardDetail] = useState("");

  //    ,    .
  const addSubmitHandler = async (e) => {
    e.preventDefault();

    const uptAwardData = {
      userId,
      title: awardTitle,
      description: awardDetail,
    };

    await Api.post("awards", uptAwardData);

    const updateList = await Api.get("award-lists", userId);
    setAwardLists(updateList.data.awards);

    setAddAward(false);
  };

  return (
    <Box
      component="form"
      onSubmit={addSubmitHandler}
      sx={{ mt: 1, width: 400 }}
    >
      <Stack spacing={2}>
        <TextField
          required
          label=""
          defaultValue={awardTitle}
          onChange={(e) => setAwardTitle(e.target.value)}
        />

        <TextField
          required
          label=""
          defaultValue={awardDetail}
          onChange={(e) => setAwardDetail(e.target.value)}
        />
      </Stack>

      <Stack
        direction="row"
        spacing={2}
        sx={{ mt: 2, justifyContent: "center" }}
      >
        <Button variant="contained" type="submit">
          
        </Button>{" "}
        <Button
          variant="outlined"
          type="reset"
          onClick={() => setAddAward(false)}
        >
          
        </Button>
      </Stack>
    </Box>
  );
}

export default AwardAddForm;
