import React, { useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import * as Api from "../../api";
import { DispatchContext } from "../../App";

import {
  Avatar,
  Button,
  TextField,
  Card,
  Container,
  Typography,
  Link,
  Grid,
} from "@mui/material";

//import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import AlertError from "../utils/AlertError";

const LoginForm = () => {
  const navigate = useNavigate();
  const dispatch = useContext(DispatchContext);

  const [email, setEmail] = useState(""); //useState email  .
  const [password, setPassword] = useState(""); //useState password  .
  const [errorMessage, setErrorMessage] = useState(null); //   .

  // abc@example.com  regex  .
  const validateEmail = (email) => {
    return email
      .toLowerCase()
      .match(
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      );
  };

  // validateEmail       .
  const isEmailValid = validateEmail(email);
  //  4   .
  const isPasswordValid = password.length >= 4;
  //
  //      .
  const isFormValid = isEmailValid && isPasswordValid;

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      // "users/login"  post.
      const res = await Api.post("users/login", {
        email,
        password,
      });

      //   response data.
      const user = res.data.user;

      // JWT    token.
      const jwtToken = user.token;
      // sessionStorage "userToken"  JWT  .
      sessionStorage.setItem("userToken", jwtToken);
      // dispatch      .
      dispatch({
        type: "LOGIN_SUCCESS",
        payload: user,
      });

      //   .
      navigate("/", { replace: true });
    } catch (err) {
      setErrorMessage(err.response.data.error.message);
    }
  };

  const guestLoginBtnClick = async (e) => {
    try {
      // "users/login"  post.
      const res = await Api.post("users/login", {
        email: process.env.REACT_APP_GUEST_ID,
        password: process.env.REACT_APP_GUEST_PW,
      });

      //   response data.
      const user = res.data.user;

      // JWT    token.
      const jwtToken = user.token;
      // sessionStorage "userToken"  JWT  .
      sessionStorage.setItem("userToken", jwtToken);
      // dispatch      .
      dispatch({
        type: "LOGIN_SUCCESS",
        payload: user,
      });

      //   .
      navigate("/", { replace: true });
    } catch (err) {
      setErrorMessage(err.response.data.error.message);
    }
  };

  return (
    <Container component="main" maxWidth="xs">
      <Card
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          backgroundColor: "white",
          padding: 2,
          borderRadius: 2,
        }}
      >
        <Avatar
          src="/logo.png"
          variant="square"
          alt="logo"
          sx={{ width: 128, height: 128, mb: 0 }}
        />
        <Typography sx={{ fontSize: "20px" }}></Typography>

        <TextField
          margin="normal"
          name="email"
          label=" "
          fullWidth
          autoComplete="email"
          required
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />

        <TextField
          margin="normal"
          name="password"
          label=""
          type="password"
          fullWidth
          autoComplete="current-password"
          required
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />

        {errorMessage && <AlertError message={errorMessage} />}

        <Button
          type="submit"
          name="LOGIN"
          fullWidth
          variant="contained"
          sx={{ mt: 3, mb: 1 }}
          onClick={handleSubmit}
        >
          
        </Button>
        <Button
          type="submit"
          name="GUEST"
          fullWidth
          variant="contained"
          color="secondary"
          sx={{ mb: 2 }}
          onClick={guestLoginBtnClick}
        >
           
        </Button>
        <Grid container>
          <Grid item xs>
            <Link> </Link>
          </Grid>
          <Grid item>
            <Link onClick={() => navigate("/register")}></Link>
          </Grid>
        </Grid>
      </Card>
    </Container>
  );
};

export default LoginForm;
